//import $, { timers } from 'jquery'
import $ from 'jquery'

/**
 * Constants
 * ====================================================
 */
const NAME = 'OsmpSidebar'
const JQUERY_NO_CONFLICT = $.fn[NAME]

/**
 * Class Definition
 * ====================================================
 */
class OsmpSidebar {

  sidebaMenuItems = [
    { pageName: "My Workspace", pageUrl: "myWorkspace.html", subMenuItems: [] },
    { pageName: "Dashboard", pageUrl: "dashboard.html", subMenuItems: [] },
    { pageName: "Calendar", pageUrl: "calendar.html", subMenuItems: [] },
    { pageName: "客户管理", pageUrl: "#", subMenuItems: [
        { href: "custRelationshipMgr.html", name: "客户关系管理" },
      ]
    },
    { pageName: "项目管理", pageUrl: "#", subMenuItems: [
        { href: "pmmProjectMgr.html", name: "项目基本信息" },
        { href: "pmmTaskBoard.html", name: "项目任务看板" },
        { href: "pmmProjectTimesheet.html", name: "项目工时管理" },
      ]
    },
    { pageName: "人员管理", pageUrl: "#", subMenuItems: [
        { href: "osmOnsiteStaffMgr.html", name: "驻场人员基本信息" }
      ]
    },
    { pageName: "招聘管理", pageUrl: "#", subMenuItems: [
        { href: "rcmDailyReport.html", name: "招聘进展日报" },
        { href: "rcmProjectReport.html", name: "招聘项目报告" },
        { href: "rcmCandidateMgr.html", name: "人才信息库" }
      ]
    },
    { pageName: "工具和设置", pageUrl: "#", subMenuItems: [
        { href: "cfgStaffDailyReport.html", name: "日报提取工具" }
      ]
    },
    { pageName: "系统管理", pageUrl: "#", subMenuItems: [
        { href: "sysUser.html", name: "用户管理" },
        { href: "sysDepartment.html", name: "部门管理" },
        { href: "sysRole.html", name: "权限管理" },
        { href: "sysBaseData.html", name: "基础数据" },
        { href: "sysParameter.html", name: "系统参数" },
        { href: "cfgUserSecurity.html", name: "个人设置" },
      ]
    },
  ];

  constructor(options) {
    this._initSidebar();
  }

  // Public
  /**
   *  <li>                           | parent    addClass("menu-open")
   *     <a href='#'>                |           addClass('active')
   *     <ul>                        | parent    css("display", "block")
   *       <li>                      | parent
   *         <a href=activedMenuUrl> |           menuUrl
   * 
   * @param {String} activedUrl Current web page file name
   * @return {void}
   */
  activeItem(activedUrl) {
    var menuPrefix = ["pmm", "osm", "rem", "rcm", "sys", "cfg", "cus"];
    if (menuPrefix.indexOf(activedUrl.substring(0, 3)) > -1) {
      let menuItem = $('[href="' + activedUrl + '"]');
      menuItem.addClass("active");
      let subUL = menuItem.parent().parent();
      subUL.css("display", "block");
      subUL.parent().addClass("menu-open");
      subUL.parent().children("a").addClass('active');
    } else {
      $("[href='" + activedUrl + "']").addClass("active");
    }

    if(localStorage.getItem("osmp.lte.pushmenu.collapse") == "true") {
      $('body').removeClass('sidebar-open')
      $('body').addClass('sidebar-collapse')
    } else {
      $('body').removeClass('sidebar-collapse')
      $('body').addClass('sidebar-open')
    }

    // Modify UI
    //$('.nav-sidebar').addClass('text-sm')
    //$('.nav-sidebar').addClass('nav-compact')
    $('body').addClass('layout-fixed text-sm')
    // $('body').addClass('layout-footer-fixed')
    // $(window).trigger('resize')
  }



  // Private
  _initSidebar() {
    // Brand Logo & Text
    $('.brand-text').html('OSMP');

    // Sidebar Menu
    var $sidebarMenu = $("<ul />", {
      // sidebar 主样式
      class: "nav nav-pills nav-sidebar flex-column nav-compact",
      role: "menu",
      "data-widget": "treeview",
      "data-accordion": "false"
    })
    $('.mt-2').append($sidebarMenu);

    this.sidebaMenuItems.forEach(e => {
      this._addMutipleSidebarMenuItem($sidebarMenu, e.pageName, e.pageUrl, e.subMenuItems);
    })
  }

  _addMutipleSidebarMenuItem(domElement, parentItemName, parentUrl, subMenus) {
    if (parentUrl != "#") {
      let clsActive = ""
      // isActived==true?clsActive="active":clsActive="";
      var $singleItem = $("<li />", { class: "nav-item" })
      var $suba = $("<a />", { class: "nav-link " + clsActive, href: parentUrl })
      $suba.append("<i class=\"nav-icon fas fa-circle text-info\"></i>")
      $suba.append("<p>" + parentItemName + "</p>");
      $singleItem.append($suba);
      // Append to sidebar
      domElement.append($singleItem);
    } else {
      var $multiItem = $("<li />", { class: "nav-item" });
      var $suba = $("<a />", { href: "#", class: "nav-link" });
      $suba.append("<i class=\"nav-icon fas fa-plus-square\"></i>");
      $suba.append("<p>" + parentItemName + "<i class=\"right fas fa-angle-left\"></i></p>");
      $multiItem.append($suba);

      // Append sub menu items
      var $subul = $("<ul />", { class: "nav nav-treeview" });
      subMenus.forEach(e => {
        var $subli = $("<li />", { class: "nav-item" });
        var $suba = $("<a />", { href: e.href, class: "nav-link" });
        $suba.append("<i class=\"far fa-circle nav-icon\"></i>");
        $suba.append("<p>" + e.name + "</p>");
        $subli.append($suba);
        $subul.append($subli);
      });
      $multiItem.append($subul);
      // Append to sidebar
      domElement.append($multiItem);
    }
  }


  // Static
  static _jQueryInterface(options) {
    var osmpSidebar = new OsmpSidebar();
    // Add active class to the opened menu item.
    var a = top.location.pathname.split("/");
    osmpSidebar.activeItem(a[a.length-1]);
  }
}

/**
 * Init common methods.
 */
OsmpSidebar._jQueryInterface.call();

/**
 * jQuery API
 * ====================================================
 */
$.fn[NAME] = OsmpSidebar._jQueryInterface
$.fn[NAME].Constructor = OsmpSidebar
$.fn[NAME].noConflict = function () {
  $.fn[NAME] = JQUERY_NO_CONFLICT
  return OsmpSidebar._jQueryInterface
}

export default OsmpSidebar

